'use client';

import { useState } from 'react';
import { Plus, Trash2, Edit, Activity, CheckCircle, XCircle } from 'lucide-react';

interface Model {
  id: string;
  name: string;
  type: 'llm' | 'nlu' | 'asr' | 'tts';
  provider: string;
  status: 'active' | 'inactive';
  endpoint?: string;
  description?: string;
}

export default function ModelsPage() {
  const [models] = useState<Model[]>([
    {
      id: 'model_llama_3_8b',
      name: 'Llama 3 8B',
      type: 'llm',
      provider: 'vLLM',
      status: 'active',
      endpoint: 'http://localhost:8000/v1',
      description: 'Primary conversational model',
    },
    {
      id: 'model_gpt4',
      name: 'GPT-4',
      type: 'llm',
      provider: 'OpenAI',
      status: 'active',
      endpoint: 'https://api.openai.com/v1',
      description: 'Fallback for complex queries',
    },
    {
      id: 'nlu_food_v1',
      name: 'Food NLU Model',
      type: 'nlu',
      provider: 'Custom',
      status: 'active',
      endpoint: 'http://localhost:5000/classify',
      description: 'Intent classification for food orders',
    },
    {
      id: 'asr_whisper',
      name: 'Whisper ASR',
      type: 'asr',
      provider: 'OpenAI',
      status: 'active',
      endpoint: 'http://localhost:9000/asr',
      description: 'Speech-to-text for voice orders',
    },
    {
      id: 'tts_elevenlabs',
      name: 'ElevenLabs TTS',
      type: 'tts',
      provider: 'ElevenLabs',
      status: 'active',
      endpoint: 'https://api.elevenlabs.io',
      description: 'Text-to-speech for voice responses',
    },
  ]);

  const [filter, setFilter] = useState<string>('all');

  const filteredModels = models.filter((model) => 
    filter === 'all' ? true : model.type === filter
  );

  const getTypeColor = (type: string) => {
    switch (type) {
      case 'llm': return 'bg-blue-100 text-blue-700';
      case 'nlu': return 'bg-purple-100 text-purple-700';
      case 'asr': return 'bg-green-100 text-green-700';
      case 'tts': return 'bg-orange-100 text-orange-700';
      default: return 'bg-gray-100 text-gray-700';
    }
  };

  return (
    <div className="space-y-6">
      {/* Header */}
      <div className="flex items-center justify-between">
        <div>
          <h1 className="text-2xl font-bold text-gray-900">Models Registry</h1>
          <p className="text-gray-600 mt-1">
            Manage LLMs, NLU models, ASR, and TTS providers
          </p>
        </div>
        <button className="flex items-center gap-2 px-4 py-2 bg-gradient-to-r from-[#059211] to-[#047a0e] text-white rounded-lg hover:shadow-lg transition-all">
          <Plus size={20} />
          Add Model
        </button>
      </div>

      {/* Filters */}
      <div className="flex gap-2 overflow-x-auto pb-2">
        {[
          { id: 'all', name: 'All Models', count: models.length },
          { id: 'llm', name: 'LLMs', count: models.filter(m => m.type === 'llm').length },
          { id: 'nlu', name: 'NLU', count: models.filter(m => m.type === 'nlu').length },
          { id: 'asr', name: 'ASR', count: models.filter(m => m.type === 'asr').length },
          { id: 'tts', name: 'TTS', count: models.filter(m => m.type === 'tts').length },
        ].map((tab) => (
          <button
            key={tab.id}
            onClick={() => setFilter(tab.id)}
            className={`px-4 py-2 rounded-lg font-medium whitespace-nowrap transition-all ${
              filter === tab.id
                ? 'bg-[#059211] text-white'
                : 'bg-white border-2 border-gray-200 hover:border-[#059211]'
            }`}
          >
            {tab.name}
            <span className={`ml-2 px-2 py-0.5 rounded-full text-xs ${
              filter === tab.id
                ? 'bg-white/20'
                : 'bg-gray-100'
            }`}>
              {tab.count}
            </span>
          </button>
        ))}
      </div>

      {/* Models Grid */}
      <div className="grid grid-cols-1 lg:grid-cols-2 gap-6">
        {filteredModels.map((model) => (
          <div
            key={model.id}
            className="bg-white rounded-xl p-6 shadow-md border-2 border-gray-100 hover:border-[#059211] transition-all"
          >
            {/* Header */}
            <div className="flex items-start justify-between mb-4">
              <div className="flex-1">
                <div className="flex items-center gap-3 mb-2">
                  <h3 className="text-lg font-bold text-gray-900">
                    {model.name}
                  </h3>
                  <span className={`px-2 py-1 rounded-lg text-xs font-medium ${getTypeColor(model.type)}`}>
                    {model.type.toUpperCase()}
                  </span>
                </div>
                <p className="text-sm text-gray-600 mb-2">{model.description}</p>
                <div className="flex items-center gap-2">
                  {model.status === 'active' ? (
                    <>
                      <CheckCircle size={16} className="text-green-600" />
                      <span className="text-sm font-medium text-green-600">Active</span>
                    </>
                  ) : (
                    <>
                      <XCircle size={16} className="text-red-600" />
                      <span className="text-sm font-medium text-red-600">Inactive</span>
                    </>
                  )}
                </div>
              </div>
              <div className="flex gap-2">
                <button className="p-2 hover:bg-gray-100 rounded-lg transition-colors">
                  <Edit size={18} className="text-gray-600" />
                </button>
                <button className="p-2 hover:bg-red-50 rounded-lg transition-colors">
                  <Trash2 size={18} className="text-red-600" />
                </button>
              </div>
            </div>

            {/* Details */}
            <div className="space-y-2 pt-4 border-t border-gray-100">
              <div className="flex items-center justify-between text-sm">
                <span className="text-gray-600">Provider:</span>
                <span className="font-medium text-gray-900">{model.provider}</span>
              </div>
              {model.endpoint && (
                <div className="flex items-center justify-between text-sm">
                  <span className="text-gray-600">Endpoint:</span>
                  <span className="font-mono text-xs text-gray-700 bg-gray-50 px-2 py-1 rounded">
                    {model.endpoint}
                  </span>
                </div>
              )}
              <div className="flex items-center justify-between text-sm">
                <span className="text-gray-600">Model ID:</span>
                <span className="font-mono text-xs text-gray-700">{model.id}</span>
              </div>
            </div>

            {/* Actions */}
            <div className="flex gap-2 mt-4 pt-4 border-t border-gray-100">
              <button className="flex-1 flex items-center justify-center gap-2 px-4 py-2 bg-gray-50 hover:bg-gray-100 rounded-lg transition-colors text-sm font-medium">
                <Activity size={16} />
                Test Model
              </button>
              <button className="flex-1 px-4 py-2 bg-gradient-to-r from-[#059211] to-[#047a0e] text-white rounded-lg hover:shadow-lg transition-all text-sm font-medium">
                Configure
              </button>
            </div>
          </div>
        ))}
      </div>

      {/* Empty State */}
      {filteredModels.length === 0 && (
        <div className="text-center py-12">
          <div className="text-6xl mb-4">ðŸ¤–</div>
          <h3 className="text-xl font-bold text-gray-700 mb-2">
            No models found
          </h3>
          <p className="text-gray-600">
            Add your first {filter === 'all' ? 'model' : filter.toUpperCase()} to get started
          </p>
        </div>
      )}
    </div>
  );
}
